@import "./variables.scss";

@mixin text($fontFamily, $fontSize, $lineHeight) {
  font-family: $fontFamily;
  color: $textColor;
  font-size: $fontSize;
  line-height: $lineHeight;
}

@mixin photo() {
  background-color: $textColor;
  background-size: cover;
  background-position: center;
}
